<template>
  <div>
    <core-toolbarinstitution />
    <v-navigation-drawer
      app
      expand-on-hover
    >
      <v-list dense>

        <v-list-item class="px-2">
          <v-list-item-icon>
            <v-icon>mdi-chevron-double-right</v-icon>
          </v-list-item-icon>
        </v-list-item>

        <v-list-item
          v-for="link in links"
          :key="link.text"
          :to="link.to"
          link
        >
          <v-list-item>
            <v-list-item-title>{{ link.text }}</v-list-item-title>
          </v-list-item>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>


    <v-container
      fill-height
      fluid
      grid-list-xl
    >
      <v-layout
        justify-center
        wrap
      >
        <material-card
          color="blue"
          title="Program"
        >
          <v-form>
            <v-container py-0>
              <v-layout wrap>
                <v-flex
                  xs12
                  md4
                >
                  <v-text-field
                    v-model="name"
                    type="string"
                    label="Program Name"
                  />
                </v-flex>

                <v-flex
                  xs12
                  md4
                >
                  <v-text-field
                    v-model="id"
                    label="Program Id"
                    disabled
                  />
                </v-flex>
                <v-flex
                  xs12
                  md4
                >
                  <v-text-field
                    v-model="email"
                    label="Institution Id"
                    disabled
                  />
                </v-flex>
                <v-flex
                  xs12
                  md4
                >
                  <label>Program Type</label>
                  <div style="border: 2px solid blue; width: 80px">
                    <select
                      v-model="password"
                      class="form-control"
                    >
                      <option
                        v-for="item in typeOptions"
                        :key="item.id"
                        :value="item.val"
                      >
                        {{ item.val }}
                      </option>
                    </select>
                  </div>
                </v-flex>
                <v-flex
                  xs12
                  md4
                >
                  <label>Method of Delivery</label>
                  <div style="border: 2px solid blue; width: 80px">
                    <select
                      v-model="location.country"
                      class="form-control"
                    >
                      <option
                        v-for="item in deliveryOptions"
                        :key="item.id"
                        :value="item.val"
                      >
                        {{ item.val }}
                      </option>
                    </select>
                  </div>
                </v-flex>
                <v-flex
                  xs12
                  md8
                >
                  <v-text-field
                    v-model="address"
                    label="Program SubType"
                  />
                </v-flex>
                <v-flex
                  xs12
                  md4
                >
                  <v-text-field
                    v-model="location.city"
                    label="Program Fees"
                  />
                </v-flex>

                <v-flex
                  xs12
                  md4
                >
                  <v-text-field
                    label="Postal Code"
                  />
                </v-flex>
                <v-flex
                  xs12
                  text-xs-right
                >
                  <v-btn
                    class="mx-0 font-weight-light"
                    color="blue"
                    href="/editProgram/"
                    @click="updateProgram"
                  >
                    Update Program
                  </v-btn>
                </v-flex>
              </v-layout>
            </v-container>
          </v-form>
        </material-card>

        <material-card
          color="blue"
          width="1225"
          title="Programs"
        >
          <v-card-title>
            <v-text-field
              v-model="search"
              label="Search"
              single-line
              hide-details
            />
          </v-card-title>

          <v-data-table
            :headers="headers"
            :items="items"
            :single-expand="singleExpand"
            :expanded.sync="expanded"
            item-key="name"
            show-expand
            class="elevation-1"
            @click:row="displayRow"
          >
            <template v-slot:top>
              <v-toolbar flat>
                <v-toolbar-title>Program Table</v-toolbar-title>
                <v-spacer />
              </v-toolbar>
            </template>
            <template v-slot:expanded-item="{ item }">
              <table @click:row="carryCourse">
                <th>Course Name</th> <th>Course ID</th> <th>Course Type</th> <th>Start Date</th> <th>End Date</th>
                <tr
                  v-for="course in item.courses"
                  :key="course.id"
                  @click="carryCourse(course)"
                >
                  <td>{{ course.name }}</td><td>{{ course.id }}</td><td>{{ course.courseType }}</td> <td>{{ course.startDate }}</td> <td>{{ course.endDate }}</td>
                </tr>
              </table>
            </template>
          </v-data-table>
        </material-card>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
    const axios = require('axios')

    export default {
        name: 'InstitutionLoggedIn',
        data () {
          return {
            id: '',
            name: '',
            phoneNumber: '',
            email: '',
            password: '',
            location : {
              city: '',
              country: '',
              address: ''
            },
            deliveryOptions:{1: {id: 1, val: "Mail"}, 2: {id: 2, val: "Email"}, 3: {id: 3, val: "Ceremony"}},
            typeOptions:{1: {id: 1, val: "Diploma"}, 2: {id: 2, val: "Degree"}, 3: {id: 3, val: "Certificate"}},
            subTypeOptions:{1: {id: 1, val: "Course-based"}, 2: {id: 2, val: "Research"}, 3: {id: 3, val: "Technical"}},
            selectedRow: null,
            search: '',
            courseToAdd: '',
            courseToRemove: '',
            headers: [
              {
                text: 'Program Name',
                align: 'start',
                filterable: false,
                value: 'name',
              },
              { text: 'Program ID', value: 'calories' },
              { text: 'Institution ID', value: 'fat' },
              { text: 'Fees($)', value: 'protein' },
              { text: 'Method of Delivery', value: 'iron' },
            ],
            headersCourse: [
              {
                text: 'Course Code',
                align: 'start',
                filterable: false,
                value: 'name',
              },
              { text: 'Course ID', value: 'calories' },
          { text: 'Highschool', value: 'iron' },
          { text: 'End Date', value: 'carbs' },
          { text: 'Start Date', value: 'protein' },
          ],
            items: [  ],
            logo: 'https://www.sccpre.cat/mypng/detail/332-3325815_admin-comments-transparent-background-admin-icon.png',
            links: [
              {
                to: '/addProgram',
                icon: 'mdi-account',
                text: 'Add Programs'
              },
              {
                to: '/editProgram',
                icon: 'mdi-account',
                text: 'Edit Programs'
              },
              {
                to: '/deleteProgram',
                icon: 'mdi-account',
                text: 'Delete Programs'
              }
            ]
          }
        },
      async  mounted () {
      console.log(this.$store.state.redirected);
        if(this.$store.state.redirected == true){
        console.log("hey that worked");
      var  carriedObj = this.$store.state.carriedObject;

        this.name = carriedObj.name;
           this.id = carriedObj.calories;
          this.email = carriedObj.fat;
          this.password = carriedObj.carbs;
          this.location.city = carriedObj.protein;
          this.location.country = carriedObj.iron;
          this.$store.dispatch('wasCarried');
        };
        this.$store.commit('wasCarried');
      const axiosConfig = {
          headers: {
              'Content-Type': 'application/json',
              'Authorization': 'Bearer ' + this.$store.state.token
          }
      };

      await axios.get('http://localhost:8080/institutions/allPrograms', axiosConfig).then(resp => {var display = this.items; function displayPrograms(item){ var entry = {name : item.name, calories: item.id, fat : item.institutionId, carbs : item.programType, protein : item.fees, iron:  item.methodOfDelivery, courses: item.courses}; display.push(entry); };
console.log("DATA: " + resp.data);
  resp.data.forEach(displayPrograms);

      }).catch(error => {
          console.log(error);
      })
        },
        methods: {
        displayRow(row){
        this.name = row.name;
         this.id = row.calories;
        this.email = row.fat;
        this.password = row.carbs;
        this.location.city = row.protein;
        this.location.country = row.iron;

        },
        createProgram: async function(){
        let axiosConfig = {
          headers: {
              'Content-Type' : 'application/json',
              'Authorization' : 'Bearer ' + this.$store.state.token
          }
        };
        const putData = {
        	id: this.id,
          institutionId: this.email,
          programType : this.password ,
          name: this.name,
          programSubType: this.programSubType,
          fees : this.location.city,
          methodOfDelivery : this.location.country

        };
        await axios.post('http://localhost:8080/programs', putData, axiosConfig)
            .then((response) => {
          console.log(response);
        }, (error) => {
          console.log(error);
        });
        },
        carryCourse(row){ var obj = {fat:row.institutionId, calories:row.id, carbs:row.startDate, protein:row.endDate, name:row.name};
        console.log("carrying: " + obj);

        this.$store.dispatch('gotoAndCarry', obj);
        this.$router.push('/editCourse');
        console.log("carrying!: " + obj);

        },
        deleteProgram: async function(){
        console.log("DELETING");
        let axiosConfig = {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + this.$store.state.token
            }
        };
        axios.delete('http://localhost:8080/programs/' + this.id , axiosConfig).then(resp => {
            console.log(resp);
        }).catch(error => {
            console.log(error);
        });
        },

        updateProgram: async function() {

        //let token = await this.getToken();

        let axiosConfig = {
          headers: {
              'Content-Type' : 'application/json',
              'Authorization' : 'Bearer ' + this.$store.state.token
          }
        };
        const putData = {
        	id: this.id,
          institutionId: this.email,
          programType : this.password ,
          name: this.name,
          programSubType: this.programSubType,
          fees : this.location.city,
          methodOfDelivery : this.location.country

        };

       await axios.put('http://localhost:8080/programs/' + this.id, putData, axiosConfig)
            .then((response) => {
          console.log(response);
        }, (error) => {
          console.log(error);
        });

        },
        addCourse: async function(){
        console.log('THIS THING: http://localhost:8080/programs/addCourse/' + this.id + '/' + this.courseToAdd);

        let axiosConfig = {
          headers: {
              'Content-Type' : 'application/json',
              'Authorization' : 'Bearer ' + this.$store.state.token
              }
              };
              console.log('http://localhost:8080/programs/addCourse/' + this.id + '/' + this.courseToAdd);
              await axios.put('http://localhost:8080/programs/addCourse/' + this.id + '/' + this.courseToAdd, {}, axiosConfig)
                   .then((response) => {
                 console.log(response);
               }, (error) => {
                 console.log(error);
               });
        },
        removeCourse: async function(){
        let axiosConfig = {
          headers: {
              'Content-Type' : 'application/json',
              'Authorization' : 'Bearer ' + this.$store.state.token
              }
              };
            await axios.put('http://localhost:8080/programs/removeCourse/' + this.id + '/' + this.courseToAdd, {}, axiosConfig)
                 .then((response) => {
               console.log(response);
             }, (error) => {
               console.log(error);
             });

        },
            updateInstitution() {
                const axiosConfig = {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': 'Bearer ' + this.$store.state.token
                    }
                };

                var request = {
                    name: this.name,
                    phoneNumber: this.phoneNumber,
                    email: this.email,
                    password: this.password,
                    location: {
                        city: this.location.city,
                        country: this.location.country,
                        address: this.location.address,
                        institutionId: '102'
                    }
                }

                axios.put('http://localhost:8080/institutions', request, axiosConfig).then(resp => {
                    console.log(resp);
                }).catch(error => {
                    console.log(error);
                });
            }
        },
    }
</script>
<style scoped>
.selected {
    background-color: red;
    font-weight: bold;
}
</style>
